.intra-container {
    width: 80%;
    background: linen;
    margin: 0 auto;
}

.intra-card {
    background: white;
    border-radius: 5px;
    width: 96%; 
    height: 100%;
    margin-top: 10px;
    padding: 15px;
    font-size: 1.5em;
    font-weight: 400;
    line-height: 1.5;
    color: '#212529';
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19)
}

.intra-card__empreend {
    width: 100%;
    height: 100%;
}

.intra-card__barra {
    background: steelblue;
    border-radius: 5px;
    padding: 5px;
}

.intra-card__titulo {
    color: white;
    margin-left: 5px;
    font-size: 1.5em;
    font-weight: bold;
}

.intra-card__date {
    color: white;
    margin-left: 5px;
    font-size: 0.5em;
}

.intra-body-aniv {
    display: flex;
    padding-top: 10px;
}

.intra-body-aniv__img {
    flex: 3;
}

.intra-body-aniv__text {
    flex: 7;
}

.intra-flex-center {
    justify-content: center;
    align-items: center;
    margin: 10px auto;
}

.intra-body-aniv__nome {
    font-size: '1.3em';
    font-weight: bold;
}

.intra-body-aniv__local {
    font-size: 0.6em;
}

.intra-body-aniv__date {
    font-size: 0.8em;
}

.intra-body-aniv__parabens {
        font-size: 1.5em;
        color: #00CC00;
        font-weight: bold;
}

.intra-card__texto {
    text-align: justify;
}

.intra-img-all {
    width: 100%;
    height: 100%;
    margin-top: 15px;
}

.intra-img-small {
    width: 100%;
    min-width: 50px;
    max-width: 150px;
    height: 100%;
    margin-right: 15px;
    margin-top: 15px;
}

.intra-img-empreend {
    width: 100%;
    height: 100%;
    max-width: 150px;
}

.intra-flex3 {
    flex: 3;
}

.intra-flex4 {
    flex: 4;
}

.intra-flex5 {
    flex: 5;
}

.intra-flex6 {
    flex: 6;
}

.intra-flex7 {
    flex: 7;
}

@media(max-width: 1000px) {

    .intra-container {
        width: 90%;
    }

    .intra-body-aniv__parabens {
        font-size: 1.5em;
    }

}

@media(max-width: 800px) {

    .intra-container {
        width: 100%;
    }

    .intra-card {
        font-size: 1.2em;
    }

    .intra-card__titulo {
        font-size: 1.2em;
    }

    .intra-body-aniv__parabens {
        font-size: 1.1em;
    }
}
